import React from 'react';

import {
  Link,
  NavLink
} from "react-router-dom";

function Header() {
  return (
    <header className="flex">
        <div>
        <nav>
             <ul>
               <li>
                 <NavLink to="/" activeClassName="active" to="/" exact>
                  Home
                </NavLink>
              </li>
              <li>
                <NavLink to="/characters">
                 Characters
                </NavLink>
              </li>
              <li>
                <NavLink to="/about">
                  About
                </NavLink>
              </li>
             </ul>
         </nav>
        </div>
         
    </header>
  );
}


export default Header;